/* Login.css */
body {
  margin: 0;
  font-family: Noto Sans, sans-serif;
}

.grid-container {
  display: grid;
  grid-template-areas:  
    ". . ."
    "a f b"
    ". . .";
  grid-template-columns: 410px auto 410px;
  grid-template-rows: 140px auto 140px;
  height: 100vh;
} 

.shadowFrame {
  display: flex;  
  grid-area: f; 
  background-color: #ffffff  ;

  justify-content: center;
  align-items: center;

  border-radius: 50px;
  box-shadow: 4px 4px 29px 8px rgba(0, 0, 0, 0.25);

  flex-direction: column;

  gap: 10px;
}

.frame6{
  display: flex;
  width: 503px;
  flex-direction: column;
  align-items: center;
  gap: 6px;

}

.IdPwLogIn {
  display: flex;
  width: 503px;
  height: 53px;
  box-sizing: border-box; 

  padding: 10px 26px;

  border-radius: 15px;
  background: rgba(167, 167, 167, 0.53);
}


.IdPwLogIn.loginButton {
  background: #AF82EC;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 24px;
  font-weight: 700;
  border: none;
  cursor: pointer;
}

.InputField {
  width: 100%;
  background: transparent;
  color: white;
  font-size: 24px;
  font-weight: 600;
  outline: none;
}

.InputField::placeholder {
  color: white;
  font-weight: 700;
}
 

.textButton {
  background: none;
  border: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  padding: 0;
  text-decoration: underline;
  transition: font-weight 0.1s ease;
}

.textButton:hover {
  font-weight: 700;
}

.frame1 {
  display: flex;
  width: 490px;
  justify-content: space-between;
}


