.OnBox {
  width: 58px;
  height: 58px;
  box-sizing: border-box;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  display: flex;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.OnBoxActive {
  background-color: rgb(188, 93, 255);
}

.OnBoxInactive {
  background-color: rgb(255, 255, 255, 0);
}

.OnIconStyle {
  fill: rgb(255, 255, 255);
  transition: fill 0.3s;
}

.OffIconStyle {
  fill: rgb(255, 255, 255);
  transition: fill 0.3s;
}

.OnBox:not(.OnBoxActive):hover {
  background-color: rgba(188, 93, 255, 0.582);
}

.OnBox:not(.OnBoxActive):hover .OffIconStyle {
  fill: rgb(255, 255, 255);
}

.SideBar {
  width: 116px;
  align-self: stretch;
  padding-left: 19px;
  padding-right: 19px;
  padding-top: 25px;
  padding-bottom: 25px;
  background: rgba(0, 0, 0, 0.81);
  border-radius: 20px;
  box-sizing: border-box;
  overflow: hidden;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.UpperFrame {
  align-self: stretch;
  height: 329px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 55px;
  display: flex;
}

.Profile {
  width: 58px;
  height: 58px;
  position: relative;
  background-image: url("./icon/Vector.svg");
  background-size: 43.5px;
  background-position: center;
  background-repeat: no-repeat;
}

.MainIconFrame {
  width: 58px;
  height: 216px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 21px;
  display: flex;
}

.Setting {
  width: 58px;
  height: 137px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 21px;
  display: flex;
}
