.checkboxFrame {
  display: flex;
  align-items: center;
  gap: 8px;
}
.checkbox {
  width: 18px;
  height: 18px;
  border: 1.5px solid #af82ec;
  border-radius: 3px;
  background-color: #ffffff;
  appearance: none;
  cursor: pointer;
  transition: background-color 0.3s, border-color 0.3s;
}

.checkbox:checked {
  background-color: #af82ec;
  border-color: #af82ec;
}

.checkbox:checked::after {
  content: "âœ”";
  color: #ffffff;
  width: 10px;
  height: 10px;
  background-color: transparent;
  position: relative;
  top: 0.1px;
  left: 2px;
}

.checkboxLabel {
  color: black;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  position: relative;
  top: 1px;
}

.checkboxLabel:hover {
  font-weight: 600;
}

.logoText {
  color: #000;
  font-size: 48px;
  font-weight: 800;
}
